import interfaces from '../../../utils/interface.js';







import popupPay from '../../../components/popupPay.vue';
export default {
	data() {
		return {
			car_goodsInfo: [],
			goodsInfo: [],
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			count: '',
			sum: '', //总价
			addressData: {
				shou_name: '',
				shou_tell: '',
				province: '',
				area: '',
				address: '',
				mo_ren: ''
			},
			showPay: false,
			set_dizhi: '',
			key: false,
			game: false,
			order_num : '', //订单号
			isGame : false , //是否为游戏

		}
	},
	onLoad(option) {
		//商品数据
		//let data = JSON.parse(option.data);

		if (option.car_goodsInfo) { //购物车结算
			this.key = true;
			this.goodsInfo.push(JSON.parse(option.car_goodsInfo));
		} else {
			this.goodsInfo.push(JSON.parse(option.goodsInfo));
		}
		this.goodsInfo.forEach(goods => {
			this.count += goods.num;
			this.sum = goods.num * goods.price_one;
			if (goods.game == '拍卖') {
				this.game = true;
				this.sum = goods.kanMoney * goods.num;
			}
			this.sum = this.sum.toFixed(2);
		})
		this.initData();
	},
	components: {
		popupPay
	},
	methods: {
		async initData() {
			this.request({
				url: interfaces.get_address, //获取收货地址
				method: "POST",
				success: ((res) => {

					if (res.error_code === 0) {
						this.addressData = res.data.filter((item) => {
							return item.mo_ren == 1; //获取默认地址
						});
						this.addressData = this.addressData[0]
						this.set_dizhi = '更改地址';
					}
					if (!this.addressData.mo_ren) {
						this.set_dizhi = '设置地址'
					}
				})
			});














		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type) {
			this.payType = type;
		},
		submit() {
			if (this.game) { //拍卖
				uni.showLoading({
					title: '订单生成中...'
				}, 500)
				let that = this;
				setTimeout(function() {
					that.request({
						url: interfaces.kan_gou, //购物成商品添加订单
						method: "POST",
						data: {
							kan_id: that.goodsInfo[0].kan_id, //游戏id
							user_address: that.addressData.id, //收货地址id
						},
						success: ((res) => {
							if (res.error_code == 0) { //生成成功
								that.order_num = res.order_num;
								that.isGame = true;
								uni.hideLoading()
								that.showPay = true;
								that.$refs.Series.Payment() //触发子组件方法(获取支付通道)
							}
						})
					});
				}, 1000)
			} else { //普通
				if (this.key) { //通过购物车结算
					let sum = parseInt(this.sum)
					let str = '';
					str += this.goodsInfo[0].id
					uni.showLoading({
						title: '订单生成中...'
					}, 500)
					let that = this;
					setTimeout(function() {
						that.request({
							url: interfaces.add_order, //购物成商品添加订单
							method: "POST",
							data: {
								car_ids: str, //购物车id
								user_address: that.addressData.id, //收货地址id
								all_price: sum, //总价
							},
							success: ((res) => {
								if (res.error_code == 0) { //生成成功
									that.order_num = res.order_num;
									uni.hideLoading()
									that.showPay = true;
									that.$refs.Series.Payment() //触发子组件方法(获取支付通道)
								}
							})
						});














					}, 1000)
				} else {

					let goods_id = this.goodsInfo[0].goods_id; //商品id
					let child_id = this.goodsInfo[0].id; //商品规格id
					let num = this.goodsInfo[0].num; //购买数量
					let user_address = this.addressData.id; //收货地址id 
					let all_price = parseInt(this.sum); //总价
					uni.showLoading({
						title: '订单生成中...'
					}, 500)

					let that = this;
					setTimeout(function() {

						that.request({
							url: interfaces.on_buy, //购买生成订单
							method: "POST",
							data: {
								goods_id,
								child_id,
								num,
								user_address,
								all_price,
							},
							success: ((res) => {
								if (res.error_code == 0) { //生成成功
									that.order_num = res.order_num;
									uni.hideLoading()
									that.showPay = true;
									that.$refs.Series.Payment() //触发子组件方法(获取支付通道)
								}
							})
						});
















					}, 1000)
				}
			}

			// this.showPay = true;
			// this.$refs.Series.Payment() //触发子组件方法(获取支付通道)
		},
		setPay(show) { //传入子组件的方法
			this.showPay = show;
		},
		stopPrevent() {}
	}
}
